export default {
    name: "app-bottom-modal",
    props: {
        title: {
            type: String,
            default: '',
        },
        sign: {
            default: null,
        },
        visible: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            iVisible: this.visible,
        };
    },
    watch: {
        visible(v) {
            this.iVisible = v;
        },
    },
    methods: {
        close() {
            this.iVisible = false;
            this.$emit('update:visible', this.iVisible);
        },
    },
}
